import * as React from "react"

import Container from "@material-ui/core/Container"

import { ChatLog } from "./chat_log"
import { ChatInput } from "./chat_input"
import { traceComponent, traceMetadata } from "../tracing"
import { ChatContext } from "../contexts/chat"
import { useFeatures } from "../contexts/features"

export const Chat = traceComponent(() => {
  const { messages, sendMessage } = React.useContext(ChatContext)
  const features = useFeatures()

  traceMetadata({
    features,
  })

  if (features && features.useMaterialUI) {
    return (
      <Container maxWidth="sm">
        <ChatLog messages={messages} />
        <ChatInput sendMessage={sendMessage} />
      </Container>
    )
  } else if (features && !features.useMaterialUI) {
    return (
      <div>
        <ChatLog messages={messages} />
        <ChatInput sendMessage={sendMessage} />
      </div>
    )
  } else {
    return null
  }
})

Chat.displayName = "chat"
