import * as React from "react"
import { traceComponent, traceMetadata } from "../tracing"

import { ChatBar } from "./chat_bar"
import TextField from "@material-ui/core/TextField"
import Button from "@material-ui/core/Button"
import Toolbar from "@material-ui/core/Toolbar"
import { useFeatures } from "../contexts/features"

export type UserInputProps = {
  setUser: (user: string) => void
}

export const UserInput = traceComponent(({ setUser }: UserInputProps) => {
  const [user, updateUser] = React.useState<string>("")
  const features = useFeatures()

  traceMetadata({
    features,
  })

  if (features && features.useMaterialUI) {
    return (
      <ChatBar>
        <Toolbar>
          <TextField
            placeholder="<Your Name>"
            name="set-user"
            value={user}
            onChange={e => {
              updateUser(e.target.value)
            }}
          />
          <Button
            name="submit-user"
            type="button"
            onClick={() => {
              setUser(user)
              updateUser("")
            }}
          >
            Hail Ship
          </Button>
        </Toolbar>
      </ChatBar>
    )
  } else if (features && !features.useMaterialUI) {
    return (
      <form>
        <input
          name="set-user"
          value={user}
          onChange={e => {
            updateUser(e.target.value)
          }}
        />
        <button
          name="submit-user"
          type="button"
          onClick={() => {
            setUser(user)
            updateUser("")
          }}
        >
          Set User
        </button>
      </form>
    )
  } else {
    return null
  }
})

UserInput.displayName = "user_input"
