import * as React from "react"

import TextField from "@material-ui/core/TextField"
import Toolbar from "@material-ui/core/Toolbar"
import Button from "@material-ui/core/Button"

import { traceComponent, traceMetadata } from "../tracing"
import { ChatMessage } from "../types"
import { ChatBar } from "./chat_bar"
import { useFeatures } from "../contexts/features"

export type ChatInputProps = {
  sendMessage: (message: ChatMessage) => void
}

export const ChatInput = traceComponent(({ sendMessage }: ChatInputProps) => {
  const [message, updateMessage] = React.useState<ChatMessage>({ text: "" })
  const features = useFeatures()

  traceMetadata({
    features,
  })

  if (features && features.useMaterialUI) {
    return (
      <ChatBar>
        <Toolbar>
          <TextField
            name="set-message"
            value={message.text}
            onChange={e => {
              updateMessage({ text: e.target.value })
            }}
          />
          <Button
            name="submit-message"
            type="button"
            onClick={() => {
              sendMessage(message)
              updateMessage({ text: "" })
            }}
          >
            Send Message
          </Button>
        </Toolbar>
      </ChatBar>
    )
  } else if (features && !features.useMaterialUI) {
    return (
      <form>
        <input
          name="set-message"
          value={message.text}
          onChange={e => {
            updateMessage({ text: e.target.value })
          }}
        />
        <button
          name="submit-message"
          type="button"
          onClick={() => {
            sendMessage(message)
            updateMessage({ text: "" })
          }}
        >
          Send Message
        </button>
      </form>
    )
  } else {
    return null
  }
})

ChatInput.displayName = "chat_input"
