import * as React from "react"

import Paper from "@material-ui/core/Paper"
import List from "@material-ui/core/List"
import ListItem from "@material-ui/core/ListItem"
import ListItemText from "@material-ui/core/ListItemText"
import ListItemAvatar from "@material-ui/core/ListItemAvatar"

import styled from "styled-components"

import { traceComponent, traceMetadata } from "../tracing"
import { ChatResponse } from "../types"
import { useFeatures } from "../contexts/features"

import logo from "../logo.png"

export type ChatLogProps = {
  messages: ChatResponse[]
}

const ChatLogPaper = styled(Paper)`
  padding-bottom: 36px;
  min-height: 100vh;
`

export const ChatLog = traceComponent(({ messages }: ChatLogProps) => {
  const features = useFeatures()

  traceMetadata({
    features,
  })

  if (features && features.slowDownLog) {
    for (let i = 0; i < 3e6; i++) {}
  }
  if (features && features.useMaterialUI) {
    return (
      <ChatLogPaper>
        <List>
          {messages.map(message => (
            <ListItem>
              <ListItemAvatar src={logo} />
              <ListItemText
                primary={message.user}
                secondary={message.body.text}
              />
            </ListItem>
          ))}
        </List>
      </ChatLogPaper>
    )
  } else if (features && !features.useMaterialUI) {
    return (
      <ul>
        {messages.map(message => (
          <li>
            {message.user} - {message.body.text}
          </li>
        ))}
      </ul>
    )
  } else {
    return null
  }
})

ChatLog.displayName = "chat_log"
